:root {
  --font-title: 32px;
  --font-sub-title: 24px;
  --font-contents: 14px;
  --font-sub-contents: 12px;
}

* {
  padding: 0;
  color: white;
  font-family: 'Roboto', sans-serif;
}

h1, h2, h3, h4, h5 {
  margin: 0;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

p {
  font-size: var(--font-contents);
  position: relative;
  margin: 0;
  z-index: 10;
}

[is='title'] {
  font-size: var(--font-title);
  font-weight: bold;
  padding-top: 100px;
  margin-bottom: 100px;
}

[is='contents'] {
  font-size: var(--font-contents);
}

[is='sub-title'] {
  font-size: var(--font-sub-title);
}

[is='sub-contents'] {
  font-size: var(--font-sub-contents);
}

.background-image {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-origin: content-box;
}

footer {
  border-top: 1px solid rgba(255, 255, 255, .2);
  padding-top: 100px;

  div.footer-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 50px;
  }

  [is='contents'] {
    font-weight: bold;
  }

  p:not(:last-child) {
    margin-bottom: 20px;
  }

  #snsIcon {
    width: 320px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;

    img {
      width: 28px;
      height: 28px;
      cursor: pointer;
    }
  }
}

#topBtn {
  opacity: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  width: 28px;
  height: 28px;
  right: 20px;
  bottom: 20px;
  font-size: var(--font-contents);
  font-weight: bold;
  background-color: #fff;
  color: #000;
  border: 1px solid rgba(255, 255, 255, .5);
  border-radius: 100%;
  box-shadow: 0 0 5px 3px rgba(255, 255, 255, .35);
  transition: all .5s;
}

#pop {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #000;
  position: fixed;
  width: 360px;
  height: 460px;
  left: 50%;
  top: 50%;
  padding: 10px;
  transform: translate(-50%, -50%);
  border-radius: 5px;
  border: 1px solid rgba(255, 255, 255, .2);
  z-index: 20;

  #pop-header {
    p:not(:last-child) {
      margin-bottom: 10px;
    }
  }

  #pop-footer {
    width: 100%;
    padding: 10px 0;

    p {
      cursor: pointer;
    }
  }
}



